/* src/components/Projects.css */

.projects-section {
    padding: 60px 20px;
    background: #f9f9f9;
    text-align: center;
  }
  
  .tabs {
    display: flex;
    justify-content: center;
    gap: 20px;
    flex-wrap: wrap;
    margin-bottom: 30px;
  }
  
  .tabs button {
    background-color: #eee;
    padding: 10px 20px;
    border: none;
    border-radius: 25px;
    cursor: pointer;
    font-weight: bold;
    transition: all 0.3s ease;
  }
  
  .tabs .active-tab {
    background-color: #4e6eff;
    color: white;
    transform: scale(1.05);
  }
  
  .projects-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 20px;
  }
  
  .project-card {
    width: 320px;
    background: white;
    border-radius: 12px;
    box-shadow: 0 4px 10px rgba(0,0,0,0.1);
    padding: 20px;
    text-align: left;
    transition: transform 0.3s ease;
  }
  
  .project-card:hover {
    transform: translateY(-10px);
  }
  
  .project-card h3 {
    color: #4e6eff;
    margin-bottom: 10px;
  }
  