.contact-section {
    padding: 60px 20px;
    background: #0a192f;
    color: #ccd6f6;
    text-align: center;
  }
  
  .contact-section h2 {
    font-size: 2.5rem;
    margin-bottom: 40px;
    color: #64ffda;
  }
  
  .contact-content {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  
  .contact-form {
    max-width: 500px;
    width: 100%;
    display: flex;
    flex-direction: column;
    gap: 15px;
  }
  
  .contact-form input,
  .contact-form textarea {
    padding: 12px;
    border: none;
    border-radius: 5px;
    background: #112240;
    color: #ccd6f6;
    font-size: 1rem;
  }
  
  .contact-form button {
    background: #64ffda;
    color: #0a192f;
    border: none;
    padding: 12px;
    border-radius: 5px;
    font-weight: bold;
    cursor: pointer;
    transition: 0.3s;
  }
  
  .contact-form button:hover {
    background: #52d1b4;
  }
  
  .contact-info {
    margin-top: 30px;
    font-size: 1rem;
    color: #8892b0;
    text-align: left;
  }
  
  .contact-info p {
    margin: 8px 0;
    display: flex;
    align-items: center;
    gap: 10px;
  }
  
  .contact-info a {
    color: #64ffda;
    text-decoration: none;
  }
  
  .contact-info a:hover {
    text-decoration: underline;
  }
  