.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* Responsive Layout */
.container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 1rem;
}

/* Grid defaults */
.skills-grid,
.projects-grid {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 20px;
}

/* Mobile responsiveness */
@media (max-width: 768px) {
  .hero-content,
  .about-section,
  .skills-container,
  .projects-container,
  .contact-section {
    padding: 20px;
    flex-direction: column;
    text-align: center;
  }

  .skills-grid,
  .projects-grid {
    grid-template-columns: 1fr !important;
  }

  .project-card {
    margin: 20px auto;
    width: 90%;
  }
}
