.skills-section {
    padding: 4rem 2rem;
    background: #0f172a;
    color: white;
    text-align: center;
  }
  
  .skills-section h2 {
    font-size: 2.5rem;
    margin-bottom: 2rem;
  }
  
  .skill-group {
    margin-bottom: 3rem;
  }
  
  .skill-items {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 1rem;
  }
  
  .skill-item {
    background: #1e293b;
    padding: 0.75rem 1.25rem;
    border-radius: 0.5rem;
    font-weight: bold;
    transition: all 0.3s ease;
    cursor: default;
  }
  
  .skill-item:hover {
    background: #38bdf8;
    transform: scale(1.1);
  }
  